import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor
} from '@angular/common/http';
import { AuthenticationService } from './authentication.service';
import { Observable } from 'rxjs/Observable';

import 'rxjs/add/operator/do';

import { HttpSentEvent, HttpErrorResponse, HttpHeaderResponse, HttpProgressEvent, HttpResponse, HttpUserEvent } from '@angular/common/http';
import { Router } from '@angular/router';

const TOKEN_HEADER_KEY = 'X-AUTH-TOKEN';

@Injectable()
export class AuthInterceptor implements HttpInterceptor {
  constructor(
    public auth: AuthenticationService,
    private router: Router
  ) {}
  /*
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
      if (!request.headers.get('Authorization')) {
    request = request.clone({
      setHeaders: {
        'X-AUTH-TOKEN': `${this.auth.token_key}`,
        'X-Requested-With': 'XMLHttpRequest'
      }
    });
    return next.handle(request);
}else {
    return next.handle(request);
}
  }*/

  intercept(req: HttpRequest<any>, next: HttpHandler):
    Observable<HttpSentEvent | HttpHeaderResponse | HttpProgressEvent | HttpResponse<any> | HttpUserEvent<any>> {
    if (this.auth.token_key) {
      console.log('set token in header ::' + this.auth.token_key);
      req.headers.set(TOKEN_HEADER_KEY, this.auth.token_key);
    }
    console.log('set XMLHttpRequest flag');
    req = req.clone({
      setHeaders: {
        'X-Requested-With': 'XMLHttpRequest'
      }
    });
    req.headers.set('X-Requested-With', 'XMLHttpRequest');
    console.log(req.headers);
    return next.handle(req).do(
      (event: HttpEvent<any>) => {
        if (event instanceof HttpResponse) {
          const token = event.headers.get(TOKEN_HEADER_KEY);
          if (token) {
            console.log('saving token ::' + token);
            this.auth.token_key = token;
          }
        }
      },
      (err: any) => {
        if (err instanceof HttpErrorResponse) {
          console.log(err);
          console.log('req url :: ' + req.url);
          if (!req.url.endsWith('/auth/user') && err.status === 401) {
            this.router.navigate(['', 'dashboard']);
          }
        }
      }
    );
  }
}



