import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';


class User {
  username: String;
  userLevel: String;
}

const LOGIN_URL = '/auth/token';
const VERIFY_URL = '/auth/token/valid';
const USER_URL = '/api/me';

@Injectable()
export class AuthenticationService {

  token_key: string;

  currentUser;

  isAuthenticated = false;

  private username: string;
  private password: string;

  constructor(
    private http: HttpClient
  ) { }

  get authHeader(): HttpHeaders {
    return new HttpHeaders()
      .set('Authorization', localStorage.getItem('auth'));
  }

  login(username: string, password: string) {
    const headers: HttpHeaders = new HttpHeaders()
      .set('Authorization', 'Basic ' + btoa(username + ':' + password));
    this.initUser();
  }

  private storeHeader(header: string): void {
    localStorage.setItem('auth', header);
  }


  initUser() {
    this.isAuthenticated = true;

    this.http.get('/api/me').subscribe(
      user => { this.currentUser = user; },
      err => { console.log(err); }
    );
  }

  // Request key -> redirect to login -A> get token in url -> parse token to auth service -> Auth boolean true

  initializeUser() {
    if (!this.token_key) {
      console.log('Error: token key is null when requesting user data.');
      return;
    } else {
      const foo: HttpParams = new HttpParams;
      foo.append('token_key', this.token_key);
      this.http.post(VERIFY_URL, null, { params: foo }).subscribe(
        (token) => { console.log('Token verfied: ' + token); },
        (err) => { console.log('Verification Failed: ' + err); }
      );
    }
  }

  private tokenLogin(headers: HttpHeaders) {
    this.http.get(LOGIN_URL, { headers: headers }).subscribe(
      (token) => {
        console.log('Recieved token: ' + token);
        this.token_key = token['key'];
      },
      (err) => { console.log(err); }
    );
  }

}



